@use '../utils/variables' as *;
@use '../utils/mixins' as *;

.title {
  font-family: Inter, Arial, Helvetica, sans-serif;
  font-size: 32px;
  font-style: italic;
  font-weight: 800;
  line-height: 32px;
  letter-spacing: -1.5px;
  justify-items: flex-end;
  color: $black-ascend-color;

  &--secondary {
    font-weight: 700;
    text-align: center;
    font-style: normal;
    padding: 96px 0 48px;
  }
}


@include for-tablets {
  .title {
    font-size: 40px;
    line-height: 40px;

    &--secondary {
      padding: 120px 0 64px;
    }
  }
}
